.newpost {
  background: #f1eadc;
  &__row {
    display: flex;
    align-items: center;
    width: auto;
    column-gap: 115px;
  }
  &__right {
    opacity: 0.6;
    height: 646px;
  }
  &__left-store {
    justify-content: center;
  }
  &__bg {
    position: absolute;
    top: 0;
    right: 0;
    z-index: -1;
  }
  &__left {
    display: flex;
    flex-direction: column;
    row-gap: 45px;
    div {
      display: flex;
      align-items: center;
    }
    &-title {
      text-align: center;
      font-weight: 500;
      font-size: 40px;
      line-height: 110%;
      letter-spacing: 0.02em;
      color: #000000;
    }
    &-desc {
      font-weight: 400;
      font-size: 20px;
      line-height: 140%;
      letter-spacing: 0.02em;
      color: #000000;
      text-align: center;
    }
    &-btn {
      padding: 20px 30px;
      font-weight: 400;
      font-size: 17px;
      letter-spacing: 0.02em;

      color: #ffffff;
      background: #6e9c9f;
      border: none;
      border-radius: 10px;
      cursor: pointer;
    }
  }
}
@media screen and (min-width: 1100px) {
  .newpost__left-btn {
    padding: 22px 50px;
  }
  .newpost__left-title {
    font-size: 53px;
  }
}
